<template>

	<view class="content">
		<textarea value="" placeholder="请在这里添加描述…" placeholder-class="faTieTextpla"  class="faTieText"/>
		<view class="uoloadView">
			<uni-file-picker limit="3" title="" @progress="success"></uni-file-picker>
		</view>
		<button type="warn" class="footBtn" @click="fabu">发贴</button>
	</view>
</template>

<script>
	import api from '../../components/service.js';
	var that
	import {
		mapState,
		mapMutations
	} from 'vuex'
	export default {
		data() {
			return {}
		},
		onHide() {

		},
		onLoad() {
			that = this
		},
		onShow() {

		},

		computed: {
			...mapState(['hasLogin', 'forcedLogin', 'userName', 'userinfo'])
		},
		methods: {
			...mapMutations(['logout', 'login']),
			getimg(img) {
				return api.getimg(img)
			},
			jump(e) {
				return api.jump(e)
			},
			// 上传成功
			success(e) {
				console.log('上传成功')
			},
			fabu(){
				uni.showToast({
					title:'发帖成功',
					success: () => {
						setTimeout(() => {
							uni.navigateBack({
								delta:1
							})
						},2000)
					}
				})
				
			}
		}
	}
</script>

<style scoped>
	page {
		background-color: #fff;
		width: 100%;
		height: 100%;
		margin: 0;
		padding: 0;
	}

	.content {
		background-color: #fff;
		width: 100%;
		height: 100%;
		margin: 0;
		padding: 0;
	}
	.faTieText{
		width: 750upx;
		height: 369upx;
		font-size: 30upx;
		font-weight: 400;
		padding: 12upx 0 0 31upx;
	}
	.uoloadView{
		padding: 0 30upx;
	}
	.footBtn {
		width: 690upx;
		margin: 0 30upx;
		border-radius: 10upx;
		margin-top: 60upx;
	}
	/deep/.faTieTextpla{
		font-size: 30upx;
		font-family: PingFang SC;
		font-weight: 400;
		color: #BBBBBB;
	}
	/deep/.icon-del-box {
		background-color: rgba(255, 255, 255, 0.5);
	}
</style>
